@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700;800&display=swap);
@import url(/variable.css);
:root{
    /* Container width */
--max-width: 100%;

    /* Background colors */
--bg-primary: #f8f8ff;
--bg-secondary: #393e46;
--bg-light: #dbd8e3;
--bg-dark: #222831;

    /* Text colors */
--text-primary: #000105;
--text-secondary: #f8f8ff;
--text-light: #053f82;
--text-dark:  #011447;

    /* Fonts */
--font-primary: 'Cabin', sans-serif;
--font-secondary: 'Open Sans', sans-serif;
}
body{
    margin: 0;
    padding: 0;
    overflow-x: hidden
}
.Search{
    
    background-color: var(--bg-dark);
    color: var(--text-secondary);
    display: flex;
    width: var(--max-width);
    height: 12rem;
    flex-direction: column;
    align-items: center;
}
.search-icon{
    width: 21px;
    
}
h1{
    font-family: var(--font-secondary);
    font-weight: 900;
    font-size: 1.9rem;
    line-height: 0ch;
}
p{
    font-family: var(--font-secondary);
    line-height: 0ch;
    font-size: medium;
    margin-bottom: 2rem;
}
form{
    background-color: white;
    width: 33rem;
    height: 2.5rem;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
}
form > input{
    width: 100%;
    border: none;
    background-color: transparent;
    outline: none;
    padding: 0 1rem;
    font-size: large;
}
form > button {
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: transparent;
    border: none;
}
.App{
    margin: 0;
}

.video-list-main-container{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-family: var(--font-secondary);
    grid-gap: 3rem;
    gap: 3rem;
    margin-left: 3rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.video-list-container{
    display: flex;
    flex-direction: row;
    align-content: center;
    grid-gap: 0;
    gap: 0;
    width: 100%;
    height: 13rem;
    word-wrap: break-word;
    overflow: hidden;
}
.video-list-container div h3{
    font-weight: 500;
    font-size: larger;
    margin-bottom: 1.3rem;
}
.video-list-container div p{
    font-weight: 700;
    line-height: 0ch;
    font-size: .9rem;
    cursor: pointer;
    margin-bottom: 1.5rem;
}
.video-list-container div span{
    font-weight: 100;
    font-size: .9rem;
    line-height: 0ch;
    cursor: text;
}
.video-list-container div small{
    font-weight: 100;
    font-size: 1rem;
    line-height: 0ch;
    cursor: text;

}
.video-list-container img{
    width: 20rem;
    height: 13rem;
    margin: 0;
    cursor: pointer;
    margin-right: 1rem;
}
.Header{
    background-color: var(--bg-dark);
    width: var(--max-width);
    height: 4.5rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
nav > .links{
    margin: 3rem;
    
}
.searchbar{
    margin-right: -45rem;
}
.video-player-main-container{
    display: flex;
    justify-content: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.video-player-container{
    display: flex;
    flex-direction: row;
    width: 90%;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.video-items-container{
    display: flex;
    flex-direction: column;
}
.video-player{
    width: 960px;
    height: 540px;
}
.video-items-container > p{
    font-weight: bold;
    cursor: pointer;
}
.video-items-container > button{
    background-color: #222831;
    cursor: pointer;
    padding: 1rem;
    border-radius: 5px;
    width: 8rem;
    margin-top: 1rem;
    color: white;
    border: 1px solid black;
    font-weight: 500;
}


.videoList-container{
    margin: 1rem;
    display: flex;
    cursor: pointer;
}
.videoList-container img{
    width: 15rem;
    height: 10rem;
}
.videoList-container > div{

    display: flex;
    flex-direction: column;
    margin-left: 1rem;
    width: 16.5rem;
    height: 10rem;
}
.videoList-container > div > h3{
    margin-bottom: 0;
}
.videoList-container > div > p > span{
    font-size: smaller;
    line-height: 1rem;
}
